.gallery {
  width: 90vw;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 15px;
}

.gallery > div {
  box-shadow: 0px 2px 9px -3px grey;
  border-radius: 8px;
}

img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px 8px 0px 0px;
}
.image-info {
  padding: 15px;
}

h2.image-title {
  font-size: 1.2em;
  text-transform: capitalize;
}

li {
  margin-bottom: 4px;
}
span.tag {
  padding: 4px 8px;
  background: #8dc9dc;
  margin-right: 5px;
  border-radius: 10px;
  display: inline-block;
}
h2.no-imge-found {
  font-size: 4em;
  text-transform: capitalize;
}